import Page from 'components/Page';
import React from 'react';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
} from 'reactstrap';
import { importTrucks, findTrucks} from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import MUIDataTable from "mui-datatables";
import CustomToolbarSelectTrucks from "../components/CustomToolbarSelectTrucks";
class ImportTrucksPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          file : '',
          xtoken:'',
          msg: '',
          error: '',
          downloadFile:true,
          data: [],
        };
        this.csvFileInput = React.createRef();
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findTrucks(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    data:response.data,
                });
            }).catch((respError) => {});
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    rerenderParentCallback = () => {
      findTrucks(this.state.xtoken).then((response) => {
        this.setState({
            ...this.state,
            data:response.data,
        });
      }).catch((respError) => {
      });
    }
    
    handleFileInput = (event) => {
        const v = event.target.files[0];
        this.setState({...this.state, file: v,});
    }
    handleSaveChanges = () => {
        const formData = new FormData();
        formData.append('csv', this.state.file);
        importTrucks(this.state.xtoken, formData)
        .then((response) => {
            if( response.status === 200)
            {
                this.setState({
                    ...this.state,
                    file : '',
                    msg: response.message,
                    data: response.data,
                    error: '',
                });
                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    msg: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
            });
            console.log('error', error);
        });
    }
    render() {
        const columns = [
            {
              name: 'Id',
              options: {
                display: false
              }
            },
            {
              name: 'Oid',
              options: {
                display: false
              }
            },
            'Truck No.',
            'Truck Owner',
            'Truck Make',
            'Truck VIN',
        ];

        const data2 = () => {
          let rtn = [];
          this.state.data.map(item => {
            rtn.push([
              item.id,
              item.owner,
              item.number,
              item.company,
              item.make,
              item.vin,
            ]);
          });
          return rtn;
        }
      
        const options = {
            filter: true,
            selectableRows: 'single',
            selectableRowsOnClick:true,
            filterType: 'dropdown',
            responsive: 'vertical',
            draggableColumns: {
              enabled: true
            },
            rowsPerPage: 5,
            selectToolbarPlacement: 'replace',
            customToolbarSelect: (selectedRows, displayData, setSelectedRows) => (
              <CustomToolbarSelectTrucks rerenderParentCallback={this.rerenderParentCallback} selectedRows={selectedRows} displayData={displayData} setSelectedRows={setSelectedRows}  originalData={this.state.data} Xtoken={this.state.xtoken}/>
            ),
            downloadOptions: {
                filename: 'trucks-excel-format.csv',
                separator: ';',
                filterOptions: {
                  useDisplayedColumnsOnly: true,
                  useDisplayedRowsOnly: true,
                }
            },
            onDownload: (buildHead, buildBody, columns, data2) => {
              if (this.state.downloadFile) {
                let val= `${buildHead(columns)}${buildBody(data2)}`.trim();
                return val;
              }
      
              return false;
            },
            onRowSelectionChange: (currentRowsSelected, allRows, rowsSelected) => {
              // console.log('currentRowsSelected', currentRowsSelected);
            },
            onRowsDelete: rowsDeleted => {
              // console.log(rowsDeleted, 'were deleted!');
            },
            onChangePage: numberRows => {
              // console.log(numberRows);
            },
            onSearchChange: searchText => {
              // console.log(searchText);
            },
            onColumnSortChange: (column, direction) => {
              // console.log(column, direction);
            },
            onViewColumnsChange: (column, action) => {
              // console.log(column, action);
            },
            onFilterChange: (column, filters) => {
              // console.log(column, filters);
            },
            onCellClick: (cellIndex, rowIndex) => {
              // console.log(cellIndex, rowIndex);
            },
            onRowClick: (rowData, rowState) => {
              // console.log(rowData, rowState);
            },
        };
      return (
        <Page title="Import trucks" breadcrumbs={[{ name: 'Import', active: true }]}>
          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardHeader>Import Trucks</CardHeader>
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
                {this.state.msg.length > 0 && (
                    <Alert color="success">{this.state.msg}</Alert>
                )}
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                  <FormGroup>
                    <Label for="exampleFile">Upload CSV file</Label>
                    <Input ref={this.csvFileInput} onChange={this.handleFileInput} type="file" name="file" />
                    <FormText color="muted">
                      Upload a CSV file to import trucks. Make sure the file is a valid CSV with <b>number,owner,make,vin</b> as column headers.
                    </FormText>
                  </FormGroup>
                  <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleSaveChanges}>Import trucks</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>            
          </Row>

          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardBody>
                    <MUIDataTable title={'Trucks'}
                    data={this.state.data.map(item => {
                      return [
                        item.id,
                        item.owner,
                        item.number,
                        item.company,
                        item.make,
                        item.vin,
                      ]
                    })} columns={columns} options={options} />
                </CardBody>
              </Card>
            </Col>
          </Row>
    
        </Page>
      );
    }
}
export default ImportTrucksPage;
